<template>
	<div class="replace">
		<div class="left">
			<div class="left_box">
				<span v-for="item,index in arr" @click="lg(index)" :class="{active :showIndex == index}">
					{{item.title}}
				</span>
			</div>
		</div>
		<div class="right" ref="right">
			<div class="right_box">
				<span v-for="item,index in arr" :style="{height: item.height,backgroundColor: item.color }" />
				</span>
			</div>
		</div>
	</div>
</template>

<script>
	import BScroll from "better-scroll"
	export default {
		methods: {
			lg(index) {
				this.showIndex = index;
				let dis = this.arrTop[index]
				this.scroll2.scrollTo(0, -dis, 500, 'easing')
			}
		},
		created() {

		},
		mounted() {
			this.scroll1 = new BScroll('.left', {
				scrollY: true,
				click: true
			});
			this.scroll2 = new BScroll('.right', {
				scrollY: true,
				click: true
			});
			Array.from(this.$refs.right.children[0].children).forEach((item, index) => {
				this.arrTop.push(item.offsetTop)
			})

			//右侧滚动  左侧高亮
			let that = this;
			this.scroll2.on("scrollEnd", function(params) {
				let disY = Math.abs(params.y)
				that.arrTop.forEach((item, index, array) => {
					
					if(disY >= item && disY < array[index + 1]) {
						that.showIndex = index
					}
					if(disY >= item ) {
						that.showIndex = index
					}
				})
			})

		},

		data() {
			return {
				showIndex: 0,
				arrTop: [],
				arr: [{
						title: "手机",
						height: "60px",
						color: "green"
					},
					{
						title: "电脑",
						height: "120px",
						color: "blue"
					},
					{
						title: "冰箱",
						height: "140px",
						color: "tomato"
					},
					{
						title: "洗衣机",
						height: "100px",
						color: "yellow"
					},
					{
						title: "空调",
						height: "150px",
						color: "black"
					},
					{
						title: "桌子",
						height: "100px",
						color: "red"
					},
					{
						title: "娃娃",
						height: "160px",
						color: "blue"
					},
					{
						title: "手机",
						height: "60px",
						color: "green"
					},
					{
						title: "电脑",
						height: "120px",
						color: "blue"
					},
					{
						title: "冰箱",
						height: "140px",
						color: "tomato"
					},
					{
						title: "洗衣机",
						height: "100px",
						color: "yellow"
					},
					{
						title: "空调",
						height: "150px",
						color: "black"
					},
					{
						title: "手机",
						height: "60px",
						color: "green"
					},
					{
						title: "洗衣机",
						height: "100px",
						color: "yellow"
					},
					{
						title: "空调",
						height: "150px",
						color: "black"
					},
					{
						title: "手机",
						height: "60px",
						color: "green"
					}

				]
			}
		}
	}
</script>

<style scoped>
	.replace {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: flex-start;
		overflow: hidden;
		padding-top:50px;
	}
	
	.replace .left {
		width: 100px;
	}
	
	.replace .left span {
		display: block;
		width: 100%;
		height: 40px;
		line-height: 40px;
		text-align: center;
		border-right: 1px solid #333333;
		border-bottom: 1px solid #333333;
	}
	
	.replace .right {
		flex: 1;
		margin-bottom: 480px;
	}
	
	.replace .right span {
		display: flex;
	}
	
	.active {
		color: red;
	}
</style>


